#blog-content .pi-computer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;

    background-color: hsl(200, 50%, 92%);
    border-radius: 16px;
    box-shadow: 2px 2px 6px hsla(200, 10%, 10%, 0.1);

    margin: 40px 0;
}


#blog-content .pi-computer .kernel-slider {
    width: 300px;
    margin-top: 20px;
}


#blog-content .pi-computer .kernel-slider .header {
    margin: 0;
    color: hsl(200, 10%, 40%);
    font-size: 14px;
}

#blog-content .pi-computer .kernel-slider .label {
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    margin: 0;
    margin-bottom: -8px;
}

#blog-content .pi-computer .controls {
    display: grid;
    grid-template-columns: 150px 150px;
    grid-auto-flow: row;
    grid-gap: 15px;
}

#blog-content .pi-computer .estimate {
    font-size: 24px;
}

#blog-content .pi-computer canvas {
    margin-top: 30px;
    width: 500px;
    height: 500px;
}

#blog-content .pi-computer .size {
    font-size: 16px;
    color: hsl(200, 10%, 40%);
    margin: 0;
    text-align: center;
    
    margin-bottom: 25px;
}

@media (max-width: 600px) {
    #blog-content .pi-computer {
        margin: 25px -50vw;
        border-radius: 0;
        box-shadow: none;
    }

    #blog-content .pi-computer canvas {
        width: 300px;
        height: 300px;
        margin-top: 20px;
    }

}

@media print {
    #blog-content .pi-computer {
        display: none !important;
    }
}

